<style scoped>
.inputwidth {
  width: 260px;
  padding-right: 30px;
}
.frombox {
  padding-top: 20px;
}
.iteminput {
  display: inline-block;
  padding-bottom: 10px;
}
.spanMust {
  color: red;
}
.setpadding_bottom{
  padding-bottom: 15px;
  font-size: 16px;
}
</style>
<template>
  <div class="cashbox">
   
    <div>
      <div>
        <h1 class="setpadding_bottom"></h1>
        <p class="iteminput">
          <label for></label>
          <!-- <Input v-model="Payee" placeholder="" class="inputwidth" /> -->
          <Select
            v-model="PayeeID"
            filterable
            class="inputwidth"
            transfer
            :disabled="this.Payeearr.length==1?true:false"
            @on-change='changePayeeID'
          >
            <Option
              v-for="(item,index) in this.Payeearr"
              :value="item.ID"
              :key="index"
            >{{ item.Name }}</Option>
          </Select>
        </p>
         <p class="iteminput" v-if="this.Payeearr.length>1&&PayeeID=='A235E8E7773EDB26697E7E771915197D'">
          <label for><span class="spanMust">*</span>&nbsp;&nbsp;&nbsp;</label>
          <Input v-model="Anonymity" placeholder=""  class="inputwidth" />
        </p>
        <p class="iteminput">
          <label for>&nbsp;&nbsp;&nbsp;</label>
          <!-- <Input v-model="Payer" placeholder="" class="inputwidth" /> -->
          <Select
            v-model="PayerID"
            filterable
            class="inputwidth"
            transfer
            :disabled="this.Payerarr.length==1?true:false"
          >
            <Option
              v-for="(item,index) in this.Payerarr"
              :value="item.ID"
              :key="index"
            >{{ item.Name }}</Option>
          </Select>
        </p>
         <p class="iteminput" v-if="this.Payerarr.length>1&&PayerID=='A235E8E7773EDB26697E7E771915197D'">
            <label for><span class="spanMust">*</span>&nbsp;&nbsp;&nbsp;</label>
            <Input v-model="Anonymity" placeholder=""  class="inputwidth" />
        </p>
        <p class="iteminput">
          <label for><span class="spanMust">*</span>&nbsp;&nbsp;&nbsp;</label>
          <Select
            v-model="Subject"
            filterable
            class="inputwidth"
            transfer
            @on-change="change_subject"
          >
            <Option
              v-for="(item,index) in this.Subjectarr"
              :value="item.Name"
              :key="item.index"
            >{{ item.Name }}</Option>
          </Select>
        </p>

       <!--  -->
      <p class="iteminput" v-if="Subject==''&&EnterType=='out'">
        <label for><span class="spanMust">*</span></label>
        <Select v-model="TrackingNumber" filterable class="inputwidth" transfer :disabled="disabledis">
          <Option
            v-for="(item,index) in TrackingNumberarr"
            :value="item.wbCode"
            :key="item.index"
          >{{ item.wbCode }}</Option>
        </Select>
      </p>


        <p class="iteminput" v-if="Labelfee==true">
          <label for><span class="spanMust">*</span>&nbsp;&nbsp;&nbsp;</label>
          <Input v-model="Labeltotal" placeholder="" @on-blur="Labelchange(Labeltotal)"  class="inputwidth" />
        </p>
        <p class="iteminput">
          <label for><span class="spanMust">*</span>&nbsp;&nbsp;&nbsp;</label>
          <Select v-model="Currency" filterable class="inputwidth" transfer>
            <Option
              v-for="(item,index) in Currencyarr"
              :value="item.value"
              :key="item.index"
            >{{ item.name }}</Option>
          </Select>
        </p>
        <p class="iteminput">
          <label for><span class="spanMust">*</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</label>
          <Input v-model="RightPrice" @on-blur="testprice(RightPrice)" placeholder="" class="inputwidth" />
        </p>
        <!-- <Button type="primary" @click="submit_btn"></Button> -->
         <Button type="primary" size="small"  icon="ios-cloud-upload" @click="SeletUploadFile"></Button>
        <Button size="small"  type="primary" icon="md-reverse-camera" @click="FormPhoto"></Button>
          <h3 style="padding-bottom:5px"></h3>
          <ul v-if="Files.length>0">
            <li v-for="item in Files">
              {{item.CustomName}}
              <Icon type="ios-trash" @click="handleRemove(item)"/>
            </li>
          </ul>
          <div v-else></div>
          <div style="padding:15px 0">
            <Button type="primary" size="small" icon='md-checkmark' @click="submit_btn"></Button>
          </div>
      </div>
      <div>
        <h1 class="setpadding_bottom"></h1>
        <Table stripe :columns="columns1" :data="Currents" max-height="380">
          <template slot-scope="{ row }" slot="type">
            <span>{{row.Conduct}}</span>
          </template>
          <template slot-scope="{ row }" slot="Subject">
            <span>{{row.Subject}}</span>
          </template>
          <template slot-scope="{ row }" slot="TargetName">
          <span>{{row.TargetName}}</span>
          </template>
          <template slot-scope="{ row }" slot="TrackingNumber" v-if="EnterType=='out'">
            <span>{{row.TrackingNumber}}</span>
          </template>
          <template slot-scope="{ row }" slot="RightPrice">
            <span>{{row.Price}}</span>
          </template>
          <template slot-scope="{ row }" slot="createdata">
            <span>{{row.CreateDate}}</span>
          </template>
          <template slot-scope="{ row }" slot="user">
            <span>{{row.Creator}}</span>
          </template>
        </Table>
      </div>
    </div>
  </div>
</template>
<script>
import { currency, subject, submitfee ,CgDeleteFiles,GetWaybillCodeByCarrierID} from "../../api/CgApi";
import{ SeletUploadFile,FormPhoto} from '../../js/browser'
export default {
   props: ["Currencyarr", "Subjectarr",'Payeearr','Payerarr','OrderID','WaybillID','Currents','conduct','Files','delCash','EnterType'],
  data() {
    return {
      Labeltotal:'',
      Labelfee:false, //
      spinShow:true,
      Subject: "", //
      Currency: 3, // 
      RightPrice: "", // 
      disabledis: false, //
      columns1: [
        {
            type: 'index',
            width: 60,
            align: 'center'
        },
        {
          title: "",
          slot: "type",
          align: "center"
        },
        {
          title: "",
          slot: "Subject",
          align: "center"
        },
        {
          title: "",
          slot: "TargetName",
          align: "center"
        },
        {
          title: "",
          slot: "TrackingNumber",
          align: "center"
        },
        {
          title: "",
          slot: "RightPrice",
          align: "center"
        },
        {
          title: "",
          slot: "createdata",
          align: "center",
          width:160
        },
        {
          title: "",
          slot: "user",
          align: "center"
        }
      ],
      PayeeID:"",//
      PayerID:"",//,
      Anonymity:null,//
      TrackingNumber:'', //
      TrackingNumberarr:[], //
      // Files:[],
    };
  },
  watch:{
      theUrl:{
        Currents(newValue, oldValue) {          
           //audio
          this.refreshAudio()
            //url
          this.url = (newValue!="")? this.Commonconst.recordForwardPrefix + newValue : ''
        },
        deep: true
      }
    },
  mounted() {
      // window["PhotoUploaded"] = this.changedimg;
      var WareHouseName=sessionStorage.getItem("WareHouseName")
      if(WareHouseName.indexOf('')!=-1&&this.EnterType=='in'){
          this.Subject=""
      }
      if(this.EnterType=='in'){
        this.columns1[3].title=""
        this.columns1.splice(this.columns1.findIndex(item => item.title === ""), 1)
        console.log(this.columns1)
      }else{
        this.columns1[3].title=""
      }
  },

  methods: {
    testprice(value){  //
        if(value!=''){
            // var testis=/(^([-]?)[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^([-]?)(0){1}$)|(^([-]?)[0-9]\.[0-9]([0-9])?$)/; 
           var testis= /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/
            var Result = testis.test(value)
            if(Result==false){
                this.RightPrice='';
                this.$Message.error('');
            }
        }
     },
     Labelchange(val){
      if(val!=''){
            // var testis= /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/
            var testis=/^[0-9]*$/
            var Result = testis.test(val)
            if(Result==false){
                this.RightPrice='';
                this.$Message.error('');
            }else{
               this.disabledis = true;
               this.Currency = 1;
               if(val<=10){
                 this.RightPrice=100
               }else{
                 var num1=val-10
                 var number=num1*5
                 this.RightPrice=100+number
               }
               
               
            }
      }
     },
     changePayeeID(value){
       if(this.Subject==""){
         this.TrackingNumber=''
         GetWaybillCodeByCarrierID(this.OrderID,value).then(res=>{
          this.TrackingNumberarr=res.data
        })
       }
     },
    change_subject(value) {
      if(value==''){
          this.Labelfee=true;
      }else{
         this.Labelfee=false;
      }
      var newarr = [];
      newarr = this.Subjectarr.filter(
        item => item.Name == value && item.PayQuote.Price != null
      );
      console.log(newarr);
      var obj = newarr[0];
      if (newarr.length > 0) {
        this.disabledis = true;
        this.Currency = obj.PayQuote.Currency;
        this.RightPrice = obj.PayQuote.Price;
        // this.rightprice=obj.PayQuote.Price;   //
        // this.rightprice="";   //
        // this.leftprice=obj.PayQuote.Price;
        // this.formdata.currency=obj.PayQuote.Currency;
      } else {
        if(value==''){
          this.disabledis = true;
          this.Currency = 1
        }else{
          this.disabledis = false;
          this.Currency = 3
          this.LeftPrice = ""
        }
      }
      if(value==''){
        GetWaybillCodeByCarrierID(this.OrderID,this.PayeeID).then(res=>{
          this.TrackingNumberarr=res.data
        })
      }
    },
    currency_subject() {
      //
      currency().then(res => {
        this.Currencyarr = res.obj;
      });
    },
    submit_btn() {
      var id='A235E8E7773EDB26697E7E771915197D'
      if(this.Subject==""||this.Currency==""||this.RightPrice==""||((this.PayeeID==id||this.PayerID==id)&&(this.Anonymity==null||this.Anonymity==''))){
         this.$Message.error('');
      }else{
        if(this.Subject==''&&this.Labeltotal==''){
           this.$Message.error('');
        }else{
          if(this.Subject==''&&this.TrackingNumber==''&&this.EnterType=='out'){

          }else{
            var Source=null
            if(sessionStorage.getItem("WareHouseName").indexOf('')!=-1){
              Source=''
            }else{
              Source=''
            }
          var conductdes=null
            var Catalog=null
            if(this.conduct==30||this.conduct==35||this.conduct==60){
              conductdes=''
              Catalog=""
            }else{
              conductdes=''
              Catalog=""
            }
          var fee = {
            Currency: this.Currency, //
            FeeType: this.$route.query.type, // in out
            Files: this.Files, //
            LeftPrice: this.RightPrice, // 
            OrderID: this.OrderID, //id
            Payee: this.PayeeID, //
            Payer: this.PayerID, //
            RightPrice: this.RightPrice, //
            Subject: this.Subject, //
            WaybillID: this.WaybillID,
            Quantity:this.Labeltotal,
            Anonymity:this.Anonymity,//
            Conduct:conductdes,//
            Source:Source,
            TrackingNumber:this.TrackingNumber,
            Catalog:Catalog
          };
            submitfee(fee).then(res => {
              if (res.success == true) {
                this.$Message.success("");
                this.Subject = ""; //
                this.Currency = 3; // ,
                this.LeftPrice = ""; // 
                this.Labeltotal=''
                this.Files=[],
                this.RightPrice='',
                this.Anonymity=''
                this.$emit('fatherMethod');
                this.$emit('delCash')
              } else {
                this.$Message.error(res.data);
              }
            });
          }
           
        }
        
      }
      
    },
     SeletUploadFile(){
      var data = {
        SessionID: 'Cash',
        AdminID: sessionStorage.getItem("userID"),
        Data:{
          WayBillID:null,
          WsOrderID:null,
        }
      };
        SeletUploadFile(data);
    },
    FormPhoto(){
      var data = {
        SessionID:'Cash',
        AdminID: sessionStorage.getItem("userID"),
        Data:{
          WayBillID:null,
          WsOrderID:null,
        }
      };
      FormPhoto(data)
    },
    //winfrom 
    changedimg(message) {
      var imgdata = message[0];
      var newfile = {
        CustomName: imgdata.FileName,
        ID: imgdata.FileID,
        Url: imgdata.Url,
      };
      this.Files.push(newfile)
      // alert(JSON.stringify(this.Files))
    },
       //
    handleRemove(file) {
       var data={
          id:file.ID
        }
       CgDeleteFiles(data).then(res=>{
          console.log(res)
          if(res.Success==true){
            this.$Message.success('')
             this.Files.splice(this.Files.indexOf(file), 1);
          }else{
            this.$Message.error('')
          }
      })
    },
  }
};
</script>


