<style scoped>
.inputwidth {
  width: 260px;
  padding-right: 30px;
}
.frombox {
  padding-top: 20px;
}
.iteminput {
  display: inline-block;
  padding-bottom: 10px;
}
.setpadding_bottom{
padding-bottom: 15px;
font-size: 16px;
}
.spanMust {
  color: red;
}
.tanlebox /deep/ .ivu-table-cell {
  padding-left: 5px;
  padding-right: 5px;
}
</style>
<template>
  <div class="cashbox">
    <div>
      <h1 class="setpadding_bottom"></h1>
      <p class="iteminput">
        <label for>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</label>
        <!-- <Input v-model="Payee" placeholder="" class="inputwidth" /> -->
         <Select
            v-model="PayeeID"
            filterable
            class="inputwidth"
            transfer
            :disabled="this.Payeearr.length==1?true:false"
            @on-change='changePayeeID'
          >
            <Option
              v-for="(item,index) in this.Payeearr"
              :value="item.ID"
              :key="index"
            >{{ item.Name }}</Option>
          </Select>
      </p>
      <p class="iteminput" v-if="this.Payeearr.length>1&&PayeeID=='A235E8E7773EDB26697E7E771915197D'">
        <label for><span class="spanMust">*</span>&nbsp;&nbsp;&nbsp;</label>
        <Input v-model="Anonymity" placeholder=""  class="inputwidth" />
      </p>
      <p class="iteminput">
        <label for>&nbsp;&nbsp;&nbsp;</label>
        <!-- <Input v-model="Payer" placeholder="" class="inputwidth" /> -->
        <Select
            v-model="PayerID"
            filterable
            class="inputwidth"
            transfer
            :disabled="this.Payerarr.length==1?true:false"
          >
            <Option
              v-for="(item,index) in this.Payerarr"
              :value="item.ID"
              :key="index"
            >{{ item.Name }}</Option>
          </Select>
      </p>
      <p class="iteminput" v-if="this.Payerarr.length>1&&PayerID=='A235E8E7773EDB26697E7E771915197D'">
        <label for><span class="spanMust">*</span>&nbsp;&nbsp;&nbsp;</label>
        <Input v-model="Anonymity" placeholder=""  class="inputwidth" />
      </p>
      <div class="iteminput">
        <div style="display:flex;flex-direction:row;">
          <label for style="line-height: 2;"><span class="spanMust">*</span>&nbsp;&nbsp;&nbsp;</label>
         <Cascader
          :transfer="true"
          :data="Subjectarr"
          v-model="Subject"
          @on-change="changecascader"
          class="inputwidth"
          ></Cascader>
        </div>
      </div>

      <p class="iteminput" v-if="Labelfee==true">
        <label for><span class="spanMust">*</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</label>
        <Input v-model="Quantity" placeholder="" @on-blur="changenumber(Quantity)"  class="inputwidth" />
      </p>
      <p class="iteminput">
        <label for><span class="spanMust">*</span>{{lableName}}</label>
        <Input v-model="LeftPrice" placeholder="" @on-blur="testprice(LeftPrice)" :disabled="unitprice==null?false:true"  class="inputwidth" />
      </p>
      <Button type="primary" size="small"  icon="ios-cloud-upload" @click="SeletUploadFile"></Button>
      <Button size="small"  type="primary" icon="md-reverse-camera" @click="FormPhoto"></Button>
      <h3 style="padding-bottom:5px"></h3>
      <ul v-if="Files.length>0">
        <li v-for="item in Files">
          {{item.CustomName}}
          <Icon type="ios-trash" @click="handleRemove(item)"/>
        </li>
      </ul>
      <div v-else></div>
      <div style="padding:15px 0">
        <Button type="primary" size="small" icon='md-checkmark' @click="submit_btn"></Button>
      </div>
      
    </div>
    <div class="tanlebox">
      <h1 class="setpadding_bottom"></h1>
      <Table stripe  :columns="columns1" :data="Records" max-height="380">
        <template slot-scope="{ row,index }" slot="indexs">
          <span>{{index+1}}</span>
        </template>
        <template slot-scope="{ row }" slot="type">
          <span>{{row.Conduct}}</span>
        </template>
        <template slot-scope="{ row }" slot="Subject">
         <span v-if="row.Descirption!=''">{{row.Subject}}&nbsp;-&nbsp;{{row.Descirption}}
           <em v-if="row.Quantity!=null">{{row.Quantity}}</em>
         </span>
         <span v-else>
            {{row.Subject}}
            <em v-if="row.Quantity!=null">{{row.Quantity}}</em>
         </span>
        </template>
         <template slot-scope="{ row }" slot="TargetName">
          <span>{{row.TargetName}}</span>
        </template>
         <template slot-scope="{ row }" slot="TrackingNumber" v-if="EnterType=='out'">
          <span>{{row.TrackingNumber}}</span>
        </template>
        <template slot-scope="{ row }" slot="valuationCurrency">
          <span>{{row.RecordPrice}}</span>
        </template>
        <template slot-scope="{ row }" slot="SettlementCurrency">
          <span>{{row.SettlePrice}}</span>
        </template>
        <template slot-scope="{ row }" slot="createdata">
          <span>{{row.CreateDate}}</span>
        </template>
        <template slot-scope="{ row }" slot="user">
          <span>{{row.Creator}}</span>
        </template>
      </Table>
      <!-- <div style="padding-top:15px;text-align: right;">
        <Page :total="100" show-elevator />
      </div> -->
    </div>
  </div>
</template>
<script>
import { currency, subject,submitfee,CgDeleteFiles,GetWaybillCodeByCarrierID,DeclarationJson,SorageJson} from "../../api/CgApi";
import{ SeletUploadFile,FormPhoto} from '../../js/browser'
export default {
  props: ["lableName","Currencyarr",'Payeearr','Payerarr','OrderID','WaybillID','Records','conduct','delbookeep','EnterType'],
  data() {
    return {
      Quantity:'',
      Labelfee:false, //
      Subject: [], //
      Currency: 3, // ,
      LeftPrice: "", // 
      unitprice:'',//
      disabledis: false, //
      columns1: [
        {
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: "",
          slot: "type",
          align: "center"
        },
        {
          title: "",
          slot: "Subject",
          align: "center",
          width:190
        },
        {
          title: "",
          slot: "TargetName",
          align: "center"
        },
        {
          title: "",
          slot: "TrackingNumber",
          align: "center"
        },
        {
          title: "",
          slot: "valuationCurrency",
          align: "center"
        },
        {
          title: "",
          slot: "SettlementCurrency",
          align: "center"
        },
        {
          title: "",
          slot: "createdata",
          align: "center",
          width: 160,
        },
        {
          title: "",
          slot: "user",
          align: "center"
        }
      ],
      PayeeID:"",
      PayerID:"",
      Anonymity:null,//
      // Files:[],
      showAnonymity:false,
      TrackingNumber:'', //
      TrackingNumberarr:[], //
      Subjectarr:[],//
      getselectedData:null,//
      Files:[],
    };
  },

  mounted() {
    if(this.conduct==30||this.conduct==35||this.conduct==60){
        // conductdes=''
        this.DeclarationJson()
      }else{
        // conductdes=''
        this.SorageJson()
    }
    if(this.EnterType=='in'){
      this.columns1[3].title=""
      this.columns1.splice(this.columns1.findIndex(item => item.title === ""), 1)
    }else{
      this.columns1[3].title=""
    }
  },
  methods: {
    DeclarationJson(){
        DeclarationJson().then(res=>{
          this.Subjectarr=res
        })
    },
    SorageJson(){
      SorageJson().then(res=>{
        this.Subjectarr=res
      })
    },
    testprice(value){  //
        if(value!=''){
            // var testis=/(^([-]?)[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^([-]?)(0){1}$)|(^([-]?)[0-9]\.[0-9]([0-9])?$)/; 
           var testis= /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/
           var Result = testis.test(value)
            if(Result==false){
                this.LeftPrice='';
                this.$Message.error('');
            }else{

            }
        }
     },
    changenumber(val){
      if(val!=''){
          // var testis= /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/
          var testis=/^[0-9]*$/
          var Result = testis.test(val)
          if(Result==true&&val>0){
             this.LeftPrice=this.Quantity*this.unitprice
          }else{
              // this.LeftPrice='';
              this.Quantity=''
              this.$Message.error('');
          }
      }
      // if(val!=''){
      //       // var testis= /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/
      //       var testis=/^[0-9]*$/
      //       var Result = testis.test(val)
      //       if(Result==false){
      //           this.LeftPrice='';
      //           this.$Message.error('');
      //       }else{
      //          this.disabledis = true;
      //          this.Currency = 1;
      //          if(val<=10){
      //            this.LeftPrice=100
      //          }else{
      //            var num1=val-10
      //            var number=num1*5
      //            this.LeftPrice=100+number
      //          }
               
               
      //       }
      // }
     },
    changePayeeID(value){
       if(this.Subject==""){
         this.TrackingNumber=''
         GetWaybillCodeByCarrierID(this.OrderID,value).then(res=>{
          this.TrackingNumberarr=res.data
        })
       }
     },
    change_subject(value) {
      if(value==''){
          this.Labelfee=true;
      }else{
         this.Labelfee=false;
      }
      var newarr = [];
      newarr = this.Subjectarr.filter(
        item => item.Name == value && item.PayQuote.Price != null
      );
      console.log(newarr);
      var obj = newarr[0];
      if (newarr.length > 0) {
        this.disabledis = true;
        this.Currency = obj.PayQuote.Currency;
        this.LeftPrice = obj.PayQuote.Price;
        // this.rightprice=obj.PayQuote.Price;   //
        // this.rightprice="";   //
        // this.leftprice=obj.PayQuote.Price;
        // this.formdata.currency=obj.PayQuote.Currency;
      } else {
        if(value==''){
          this.disabledis = true;
          this.Currency = 1
        }else{
          this.disabledis = false;
          this.Currency = 3
          this.LeftPrice = ""
        }
        
      }
      if(value==''){
        GetWaybillCodeByCarrierID(this.OrderID,this.PayeeID).then(res=>{
          this.TrackingNumberarr=res.data
        })
      }
    },
    //
     changecascader(value, selectedData) {
      var data=selectedData[selectedData.length-1]
      this.Quantity=''
      if(data.Isquantity==true){
          this.Labelfee=true
          this.unitprice=data.prices
          this.LeftPrice=data.prices
      }else{
          this.Labelfee=false
          this.unitprice=data.prices
          this.LeftPrice=data.prices
      }
      this.Currency=data.currency
      console.log(this.Currency)
    var egs =selectedData.reverse()
        var result = {}
        var key ='children'
        s(egs)
        function s(arrs) {
            arrs.forEach((o, i) => {
                if (!result) {
                	result= o
             	    result[key] = []
                } else {
                    o[key] = result
                    result = o
                }
             })
        }
        this.getselectedData=result
    },


    currency_subject() {
      //
      currency().then(res => {
        this.Currencyarr = res.obj;
      });
    },
    // 
    submit_btn(){
      // console.log(""+this.Currency)
      // console.log(""+this.LeftPrice)
      if((this.LeftPrice!=''&&this.LeftPrice!=null)&&this.Subject[0]!=""){
       var id='A235E8E7773EDB26697E7E771915197D'
      if(this.Labelfee==true&&this.Quantity==''){
        this.$Message.error('');
      }else{
        if(this.PayerID==id&&(this.Anonymity==null||this.Anonymity=='')){
          this.$Message.error('');
        }else{
           var Source=null
            if(sessionStorage.getItem("WareHouseName").indexOf('')!=-1){
              Source=''
            }else{
              Source=''
            }
            var conductdes=null
            var Catalog=null
            if(this.conduct==30||this.conduct==35||this.conduct==60){
              conductdes=''
              Catalog=""
            }else{
              conductdes=''
              Catalog=""
            }
            var fee = {
              Currency: this.Currency, //
              FeeType: this.$route.query.type, // in out 
              Files: this.Files, //
              LeftPrice:this.LeftPrice, // 
              OrderID:this.OrderID, //id
              Payee: this.PayeeID, //
              Payer: this.PayerID, //
              RightPrice:'', //
              Subject: this.Subject[0], //
              WaybillID:this.WaybillID,
              Quantity:this.Quantity,//
              Anonymity:this.Anonymity,//
              Conduct:conductdes,//
              Source:Source,
              TrackingNumber:this.TrackingNumber,
              Catalog:Catalog,
              Data:this.getselectedData
            };
            console.log(fee)
             submitfee(fee).then(res => {
                  if(res.success==true){
                      this.$Message.success('');
                      this.Subject=[]; //
                      this.Currency=''; // ,
                      this.LeftPrice= "";// 
                      this.Quantity=''
                      this.Files=[]
                      this.getselectedData=null,
                      this.$emit('fatherMethod');
                      this.$emit('delbookeep')
                  }else{
                      this.$Message.error(res.data);
                  }
            });
        }
      }
     }else{
       this.$Message.error('');
     }
   },
    submit_btnold() {
      var id='A235E8E7773EDB26697E7E771915197D'
      if(this.Subject==''||this.Currency==''||this.LeftPrice==''||((this.PayeeID==id||this.PayerID==id)&&(this.Anonymity==null||this.Anonymity==''))){
        this.$Message.error('');
      }else{
          if(this.Subject==''&&this.Quantity==''){
           this.$Message.error('');
          }else{
            if(this.Subject==''&&this.TrackingNumber==''&&this.EnterType=='out'){
              this.$Message.error({
                content: '',
                duration: 3
             });
            }else{
              var Source=null
            if(sessionStorage.getItem("WareHouseName").indexOf('')!=-1){
              Source=''
            }else{
              Source=''
            }
            var conductdes=null
            var Catalog=null
            if(this.conduct==30||this.conduct==35||this.conduct==60){
              conductdes=''
              Catalog=""
            }else{
              conductdes=''
              Catalog=""
            }
            var fee = {
              Currency: this.Currency, //
              FeeType: this.$route.query.type, // in out 
              Files: this.Files, //
              LeftPrice:this.LeftPrice, // 
              OrderID:this.OrderID, //id
              Payee: this.PayeeID, //
              Payer: this.PayerID, //
              RightPrice:'', //
              Subject: this.Subject, //
              WaybillID:this.WaybillID,
              Quantity:this.Quantity,//
              Anonymity:this.Anonymity,//
              Conduct:conductdes,//
              Source:Source,
              TrackingNumber:this.TrackingNumber,
              Catalog:Catalog
            };
            console.log(fee)
            submitfee(fee).then(res => {
                  if(res.success==true){
                      this.$Message.success('');
                      this.Subject=""; //
                      this.Currency=3; // ,
                      this.LeftPrice= "";// 
                      this.Quantity=''
                      this.Files=[]
                      this.$emit('fatherMethod');
                      this.$emit('delbookeep')
                  }else{
                      this.$Message.error(res.data);
                  }
            });
            }
            
         }
      }
      
    },
    SeletUploadFile(){
      var data = {
        SessionID: 'Bookkeeping',
        AdminID: sessionStorage.getItem("userID"),
        Data:{
          WayBillID:null,
          WsOrderID:null,
        }
      };
        SeletUploadFile(data);
    },
    FormPhoto(){
      var data = {
        SessionID:'Bookkeeping',
        AdminID: sessionStorage.getItem("userID"),
        Data:{
          WayBillID:null,
          WsOrderID:null,
        }
      };
      FormPhoto(data)
    },
    //winfrom 
    changed(message) {
      var imgdata = message[0];
      var newfile = {
        CustomName: imgdata.FileName,
        ID: imgdata.FileID,
        Url: imgdata.Url,
      };
      this.Files.push(newfile)
    },
       //
    handleRemove(file) {
       var data={
          id:file.ID
        }
       CgDeleteFiles(data).then(res=>{
          console.log(res)
          if(res.Success==true){
            this.$Message.success('')
             this.Files.splice(this.Files.indexOf(file), 1);
          }else{
            this.$Message.error('')
          }
      })
    },
  }
};
</script>


